import React, { Component } from "react";
import { withRouter, Link } from "react-router-dom";
import { Mutation } from "react-apollo";
import gql from "graphql-tag";
import * as Cookies from "es-cookie";
import { Helmet } from "react-helmet";


export const SIGNUP_USER = gql`
    mutation($username: String!, $email: String!, $password: String!, $firstName: String!, $lastName: String!, $profileImage: String) {
        signUp(username: $username, email: $email, password: $password, firstName: $firstName, lastName: $lastName, profileImage:$profileImage) {
            token
            user
        }
    }
`;

const initialState = {
    username: '',
    email: '',
    password: '',
    passwordConfirm: '',
    firstName: '',
    lastName: '',
    passwordMatch: '',
    error: ''
}

class SignUp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState
        }
    }

    head() {
        return (
            <Helmet bodyAttributes={{ class: 'signUp'}}>
                <title>Join now - Apollo React App</title>
            </Helmet>
        )
    }

    onChange = e => {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    };

    handleSubmit = (e, signUp, client) => {
        e.preventDefault();
        console.log(client);
        signUp().then(async ({ data }) => {
           Cookies.set('token', data.signUp.token);
           await this.props.refresh();
           this.setState({ ...initialState });
           this.props.history.push('/');
        }).catch(error => {
            this.setState({
                error: 'Either your email or username is already taken'
            });
        });
    }

    confirmPW = () => {
        const { password, passwordConfirm } = this.state;
        const isMatch = password !== passwordConfirm;
        this.setState({
            passwordMatch: isMatch
        });
    }

    validateForm = () => {
        const { firstName, lastName, email, userName, password, passwordConfirm } = this.state
        const isInvalid = !firstName || !lastName || !email || !userName || !password || password !== passwordConfirm
        return isInvalid;
    }

    render() {
        const {
            username,
            email,
            password,
            passwordConfirm,
            firstName,
            lastName,
            passwordMatch
        } = this.state;

        return (
            <div class="login-wrapper">
                {this.head()}
                <span class="title">SignUp</span>
                <Mutation 
                mutation={SIGNUP_USER} 
                variables={{username, email, password, firstName, lastName}} 
                update={(cache, { data }) => {
                    console.log("data", data);
                    console.log("cache", cache);
                }}
                >
                    {(signUp, {data, client, loading, error}) => {
                        return(
                            <form name="form" onSubmit={e => this.handleSubmit(e, signUp, client)}>
                                <fieldset>
                                    
                                    <div class="inputrow">
                                        <i class="fa icon-user fa-fw" />
                                        <input
                                        type="text"
                                        name="username"
                                        id="username"
                                        placeholder="UserName"
                                        onChange={this.onChange}
                                        onBlur={this.validateForm}
                                        value={username}
                                        autoFocus
                                        required
                                        />
                                    </div>
                                    <div class="inputrow">
                                        <i class="fa icon-user fa-fw" />
                                        <input
                                        type="text"
                                        name="firstName"
                                        id="firstName"
                                        placeholder="FirstName"
                                        onChange={this.onChange}
                                        value={firstName}
                                        required
                                        />
                                    </div>
                                    <div class="inputrow">
                                        <i class="fa icon-user fa-fw" />
                                        <input
                                        type="text"
                                        name="lastName"
                                        id="lastName"
                                        placeholder="LastName"
                                        onChange={this.onChange}
                                        value={lastName}
                                        required
                                        />
                                    </div>
                                    <div class="inputrow">
                                        <i class="fa icon-mail4 fa-fw" />
                                        <input
                                        type="email"
                                        name="email"
                                        id="email"
                                        placeholder="Email"
                                        onChange={this.onChange}
                                        value={email}
                                        required
                                        />
                                    </div>
                                    <div class="inputrow">
                                        <i class="fa icon-key fa-fw" />
                                        <input
                                        type="password"
                                        name="password"
                                        id="password"
                                        data-typetoggle="#show"
                                        onChange={this.onChange}
                                        value={password}
                                        placeholder="Password"
                                        required
                                        />
                                    </div>
                                    <div class="inputrow">
                                        <i class="fa icon-key fa-fw" />
                                        <input
                                        type="password"
                                        name="passwordConfirm"
                                        id="passwordConfirm"
                                        data-typetoggle="#show"
                                        onChange={this.onChange}
                                        onBlur={this.confirmPW}
                                        value={passwordConfirm}
                                        placeholder="Confirm Password"
                                        required
                                        />
                                    </div>

                                    
                                    <div class="button-center">
                                        <button class="btn-log" type="submit" id="submit" disabled={passwordMatch || this.validateForm()} >
                                        Register
                                        </button>
                                        {loading && (
                                        <img src="data:image/gif;base64," />
                                        )}
                                    </div>
                                </fieldset>
                            </form>
                        )
                    }}
                </Mutation>
            </div>
        )
    }
}

export default withRouter(SignUp)